.group input[type='checkbox'] {
    display: none;
}

.group label {
    position: relative;
    color: #48535f;
    font-family: 'Inter';
    font-size: 20px;
    text-transform: lowercase;
}

.group label::before {
    content: '';
    background: url(data:image/png;base64,);
    color: white;
    background-position: center;
    background-size: contain;
    width: 30px;
    height: 30px;
    position: absolute;
    left: -42px;
    top: -20px;
    transform: scale(0) rotateZ(180deg);
    transition: all 0.4s cubic-bezier(0.54, 0.01, 0, 1.49);
}

.group input[type='checkbox']:checked + label::before {
    transform: scale(1) rotateZ(0deg);
}

.group label::after {
    content: '';
    border: 5px solid #48535f;
    width: 20px;
    height: 20px;
    position: absolute;
    left: -42px;
    top: -20px;
    border-radius: 50%;
}

