    margin: 60px auto;
    font-size: 10px;
    position: relative;
    text-indent: -9999em;
    border-top: 1.1em solid rgba(28, 33, 37, 0.2);
    border-right: 1.1em solid rgba(28, 33, 37, 0.2);
    border-bottom: 1.1em solid rgba(28, 33, 37, 0.2);
    border-left: 1.1em solid #48535f;
    transform: translateZ(0);
    animation: load8 1.1s infinite linear;
}

